.el {
    background-color: #fff;
    box-shadow: var(--box-shadow);
    color: #333;
    display: flex;
    justify-content: space-between;
    position: relative;
    padding: 10px;
    margin: 10px 0;

    &.plus {
        border-right: 5px solid #2ecc71;
    }
    &.minus {
        border-right: 5px solid #c0392b;
    }

    &:hover {
        .deleteBtn {
            opacity: 1;
        }
    }
}

.deleteBtn {
    cursor: pointer;
    background-color: #e74c3c;
    border: 0;
    color: #fff;
    font-size: 20px;
    line-height: 20px;
    padding: 2px 5px;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-100%, -50%);
    opacity: 0;
    transition: opacity 0.3s ease;

    &:focus {
        outline: 0;
    }
}